import React from 'react';
import { connect } from 'react-redux';
import { deviceActions, deviceSelectors } from 'containers/Console/ducks';
import {
  AddIcon,
  DevicesIcon,
  RolesIcon,
  SettingsIcon,
} from 'components/EntityIcons';
import { Button } from 'reactstrap';
import {
  DeviceModel,
  DEVICE_COLUMNS,
  PrimitiveType,
} from 'cb-utils/console-entity-models';
import {
  createShowBaseModal,
  createShowConfirmationModal,
} from 'containers/Console/actions/ui';
import PageHeader from 'components/ConsolePageHeader';
import ConsolePage from 'components/ConsolePage';
import Helmet from 'react-helmet';
import { Column } from 'react-table';
import ConsoleCrudgridContainer from 'containers/ConsoleCrudgridContainer';
import { format } from 'date-fns';
import { FormattedMessage, injectIntl } from 'react-intl';
import messages from './messages';
import { CBDispatch } from 'cb-utils/typeUtils';
import CbDropDown from 'components/CbDropDown';
import { GearDropDown } from 'containers/EdgesPage';
import styled from 'styled-components';
import { RouteComponentProps } from 'react-router';
import { bindActionCreators } from 'redux';
import { createStructuredSelector } from 'reselect';

const TableWrapperDiv = styled.div`
  width: 100%;
  .rt-td:nth-child(2) {
    font-weight: bold;
  }
`;

const transformColumns = (cols: Column[], props: MasterProps) => {
  // isn't properly immutable change
  const newCols = cols.map((col) => {
    const colName = col.accessor;
    switch (colName) {
      case DEVICE_COLUMNS.name:
        col.Cell = (cellInfo) => {
          const val = cellInfo.row[colName];
          return (
            <span className="font-weight-bold">
              {cellInfo.row[colName]}{' '}
              <CbDropDown
                triggerEl={<GearDropDown />}
                items={[
                  {
                    displayName: (
                      <div>
                        <SettingsIcon />{' '}
                        <FormattedMessage {...messages.editViewDetails} />
                      </div>
                    ),
                    'data-test': 'edit-device-btn',
                    onClick: () =>
                      props.createShowBaseModal({
                        component: 'NewDeviceModal',
                        props: {
                          assetClass: 'devices',
                          isEdit: true,
                          device: props.devices[val],
                        },
                      }),
                  },
                  {
                    displayName: (
                      <div>
                        <RolesIcon />{' '}
                        <FormattedMessage {...messages.editRoles} />
                      </div>
                    ),
                    'data-test': 'edit-device-roles-btn',
                    onClick: () =>
                      props.createShowBaseModal({
                        component: 'EditMyRolesModal',
                        props: { assetClass: 'devices', id: val },
                      }),
                  },
                ]}
              />
            </span>
          );
        };
        return col;
      case DEVICE_COLUMNS.created_date:
        col.Cell = (cellInfo) => {
          const val = cellInfo.row[colName];
          return val && format(new Date(val * 1000), `MMM DD YYYY, hh:mm a `);
        };
        return col;
      case DEVICE_COLUMNS.device_key:
        col.show = false;
        return col;
      default:
        return col;
    }
  });
  return newCols;
};

interface DevicesPageProps extends RouteComponentProps<{ systemKey: string }> {
  totalCount: number;
  rows: string[];
}

type MyProps = DevicesPageProps & ReactIntl.InjectedIntlProps;
type MasterProps = MyProps & StateToProps & DispatchToProps;

const DevicesPage = (props: MasterProps) => {
  const systemKey = props.match.params.systemKey;
  const {
    PKColumnName,
    deleteDevice,
    deleteIsLoading,
    createIsLoading,
    customColumns,
    updateDevice,
  } = props;

  const title = (
    <PageHeader
      header={
        <div>
          <DevicesIcon /> <FormattedMessage {...messages.header} />{' '}
          <CbDropDown
            triggerEl={<GearDropDown data-cy="open-device-settings-dropdown" />}
            items={[
              {
                displayName: (
                  <div>
                    <RolesIcon /> <FormattedMessage {...messages.editRoles} />
                  </div>
                ),
                'data-test': 'edit-device-overall-roles-btn',
                onClick: () =>
                  props.createShowBaseModal({
                    component: 'EditRolesModal',
                    props: { assetClass: 'devices' },
                  }),
              },
            ]}
          />
        </div>
      }
      actionButton={() => (
        <Button
          onClick={() =>
            props.createShowBaseModal({
              component: 'NewDeviceModal',
              props: {},
            })
          }
          data-cy="add-device"
          color="primary"
        >
          <AddIcon />
          &nbsp; <FormattedMessage {...messages.addButton} />
        </Button>
      )}
    />
  );

  return (
    <ConsolePage subheader={title}>
      <Helmet
        title="ClearBlade - Devices"
        meta={[{ name: 'description', content: '' }]}
      />
      <TableWrapperDiv>
        <ConsoleCrudgridContainer
          assetClass="devices"
          systemKey={systemKey}
          deleteEventCallback={deleteDevice}
          deleteIsLoading={deleteIsLoading}
          createIsLoading={createIsLoading}
          allowSelect
          exportCallback={() =>
            props.createShowBaseModal({
              component: 'ExportModal',
              props: { assetClass: 'devices' },
            })
          }
          updateEventCallback={(
            device: DeviceModel,
            colName: string,
            val: PrimitiveType,
          ) => {
            updateDevice({
              [PKColumnName]: device[PKColumnName],
              system_key: systemKey,
              [colName]: val,
            });
          }}
          showEditColumns
          columnNames={[
            'device_key',
            'name',
            'enabled',
            'type',
            'description',
            'state',
            'created_date',
          ].concat(Object.keys(customColumns))}
          transformColumns={(cols: Column[]) => {
            return transformColumns(cols, props);
          }}
        />
      </TableWrapperDiv>
    </ConsolePage>
  );
};

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  devices: deviceSelectors.entities,
  PKColumnName: deviceSelectors.PKColumnName(),
  isLoading: deviceSelectors.fetchAll.isLoading,
  deleteIsLoading: deviceSelectors.deleteMultiple.isLoading,
  createIsLoading: deviceSelectors.create.isLoading,
  customColumns: deviceSelectors.customColumns(),
});

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;

const mapDispatchToProps = (dispatch: CBDispatch, ownProps: MyProps) => ({
  ...bindActionCreators(
    {
      fetchAllQuery: deviceActions.fetchAllQuery.request,
      createDevice: deviceActions.create.request,
      updateDevice: deviceActions.update.request,
      fetchColumns: deviceActions.fetchColumns.request,
      fetchCount: deviceActions.fetchCount.request,
      createShowBaseModal,
    },
    dispatch,
  ),
  deleteDevice: (devices: string[]) =>
    dispatch(
      createShowConfirmationModal(
        ownProps.intl.formatMessage(messages.deleteConfirmation, {
          num: devices.length,
        }),
        deviceActions.deleteMultiple.request(devices),
      ),
    ),
});

export default injectIntl(
  connect(mapStateToProps, mapDispatchToProps)(DevicesPage),
);



